              if (this.filter_color(arr[key].colors, this.picked_color)) {
                arr_2[key] = arr[key];
              }
            } else if (
              this.picked_color == 0 &&
              this.picked_discount == 0 &&
              this.picked_material != 0
            ) {
              if (
                this.filter_material(arr[key].materials, this.picked_material)
              ) {
                arr_2[key] = arr[key];
              }
            }
          }
          arr_3 = arr_2;
        } else {
          arr_3 = arr;
